import 'babel-polyfill';

export var attendeeFactory = {
    create( attendee={} ) {
        return Object.assign({
            SourceCode: '',
            AccountNumber: '',
            ID: '',
            Status: '',
            UEStatus: '',
            AttendeeType: 'Doctor',
            NameFirst: '',
            NameLast: '',
            AttendeeTitle: 'Doctor',
            Email: '',
            PhoneWork: '',
            Address1: '',
            City: '',
            State: '',
            Zip: ''
        }, attendee );
    }
};



