import {h} from 'preact';
import AttendeeDetailForm from './AttendeeDetailForm';

var AttendeeDetailEditForm = ( props ) => {
    return (
        <div className="attendee-detail-container">
            <div className="grid-unit"></div>
            <AttendeeDetailForm
                attendee={props.attendee}
                eventTuitionTable={props.eventTuitionTable}
                confirmAction={props.confirmAction}
                confirmButtonLabel="Update"
                cancelAction={props.cancelAction}/>
        </div>
    );
};

export default AttendeeDetailEditForm;



