import {h, Component} from 'preact';
import {HOC} from 'formsy-react';

//import UpiFormFieldMixin from './UpiFormFieldMixin';

class UpiSelect extends Component {

    constructor( props ) {
        super( props );

        this.handleBlur = this.handleBlur.bind( this );
        this.changeValue = this.changeValue.bind( this );

        this.state = {
            hasFocusedOut: false
        }
    }

    getSelectedValue() {
        var selectEl = this.formField;
        return selectEl[selectEl.selectedIndex].value;
    }

    // setValue() will set the value of the component, which in
    // turn will validate it and the rest of the form
    changeValue() {
        var value = this.getSelectedValue();

        this.props.onUpdate( value );
        this.props.setValue( value );
    }

    handleBlur() {
        this.setState( {
            hasFocusedOut: true
        } );
    }

    render() {
        //console.log( this.getValue() );

        var value = this.props.getValue(),
            errorMessage,
            validationClassName;

        // if has a value and the value is valid show feedback immediately
        if ( value && this.props.isValid() ) {
            validationClassName = 'valid-field';
        }

        // only show errors if field has been interacted with
        if ( this.state.hasFocusedOut ) {
            validationClassName =
                this.props.showRequired() ? 'required-field'
                    : this.props.showError() ? 'error'
                    : 'valid-field';

            // An error message is returned ONLY if the component is invalid
            // or the server has returned an error message
            errorMessage = this.props.getErrorMessage();
        }

        return (
            <div className={validationClassName}>
                <label htmlFor={this.props.name}>{this.props.label}</label>
                <select className="custom-select"
                        ref={ref => (this.formField = ref)}
                        name={this.props.name}
                        onBlur={this.handleBlur}
                        onChange={this.changeValue}
                        value={this.props.getValue()}>
                    {this.props.children}
                </select>
                <span name={this.props.name}>{errorMessage}</span>
            </div>
        );
    }
}

// UpiSelect.propTypes=  {
//     name: React.PropTypes.string.isRequired
// };

UpiSelect.defaultProps = {
    value: null,
    onUpdate: () => {
    }
};

export default HOC( UpiSelect );



