import {h} from 'preact';

var Contact = ( props ) => {
    return (
        <li className="contact" onClick={() => props.onClickHandler( props.id )}>
            {props.fullName} <i className="icon-chevron-right"></i>
        </li>
    );
}

export default Contact;



