import ServiceProvider from '../../modules/core/ServiceProvider';
import {svcNames} from '../util/constants.js';

const {GET_ATTENDEES_JSON, ADD_ATTENDEE, UPDATE_ATTENDEE, DELETE_ATTENDEE} = svcNames;

const svc = new ServiceProvider( {
    baseURL: upi.conf.svc.ECOMMERCE_API
} );

/**
 * AttendeeService
 * currently these services rely on retrieving the event ID from a cookie, if the cookie is not present the request
 *
 *
 * should handle fallback
 * @type {{getAll, add, remove, update}}
 */
export const AttendeeService = {

    /**
     * Return a list of current attendees for a given event seminar
     * @eventId {Number} - the event identifier
     */
    getAll ( eventid ) {
        return svc.get( GET_ATTENDEES_JSON, {
            params: {eventid}
        } ).then( ( {data} ) => data.Attendees );
    },

    /**
     * Add an attendee to a given event/seminar
     * @eventId {Number} - the event identifier
     * @attendeeData {Object} - attendee object {id, AccountNumber, Address1, Address2, AttendeeTitle... }
     */
    add ( attendeeData ) {
        return svc.post( ADD_ATTENDEE, {
            params: attendeeData
        } ).then( ServiceProvider.responseData );
    },

    /**
     * Remove an attendee to a given event/seminar
     * @eventId {Number} - the event identifier
     * @attendeeId {Number} - attendee identifier
     */
    remove ( attendeeId ) {
        return svc.post( DELETE_ATTENDEE, {
            params: {ID: attendeeId}
        } ).then( ServiceProvider.responseData );

    },

    /**
     * Add an attendee to a given event/seminar
     * @eventId {Number} - the event identifier
     * @attendeeData {Object} - attendee object {id, }
     */
    update ( attendeeData ) {
        return svc.post( UPDATE_ATTENDEE, {
            params: attendeeData
        } ).then( ServiceProvider.responseData );
    }
};



