import ServiceProvider from '../../modules/core/ServiceProvider';
import {svcNames} from '../util/constants.js';

const {GET_ATTENDEES_JSON} = svcNames;
const svc = new ServiceProvider( {
    baseURL: upi.conf.svc.ECOMMERCE_API
} );

function filterDuplicateContacts ( contactList ) {
    // filter out duplicate records for each contact taking the item with the highest id
    const newContacts = contactList.reduce( ( prev, curr ) => {
        let key = `${curr.NameFirst} ${curr.NameLast}`;
        // only add record if not added or current record.ID is lesser than curr.ID
        if ( !prev[key] || prev[key].ID < curr.ID ) {
            prev[key] = curr;
        }
        return prev;
    }, {} );

    return Object.values( newContacts );
}

export const ContactsService = {
    getAll () {
        return svc.get( GET_ATTENDEES_JSON,
            {
                params: {status: 'registered'}
            } )
            .then( ( {data} ) => data.Attendees )
            .then( filterDuplicateContacts );
    }
};



