import {h, Component} from 'preact';
import Contacts from './Contacts';
import AttendeeDetailForm from './AttendeeDetailForm';
import {attendeeFactory} from '../util/models';
import assign from 'object-assign';

export default class AttendeeRegistrationForm extends Component {

    constructor( props ) {
        super( props );

        this.setCurrentContact = this.setCurrentContact.bind( this );

        this.state = {
            currentContact: attendeeFactory.create( {
                Status: 'inProcess',
                SourceCode: this.props.sourceCode,
                AccountNumber: this.props.accountNumber,
                UEStatus: this.props.ueStatus || ''
            } )
        };
    }

    setCurrentContact( contactId ) {
        var currentContact = this.props.contactList.filter( contact => contact.ID === contactId )[0];

        // todo: refactor this to be DRY - same pattern used in state constructor
        currentContact = assign( {}, currentContact, {
            Status: 'inProcess',
            SourceCode: this.props.sourceCode,
            AccountNumber: this.props.accountNumber,
            UEStatus: this.props.ueStatus || ''
        } );

        this.setState( {currentContact} );
    }

    render() {
        return (
            <div className="attendee-detail-container">
                <Contacts
                    contactList={this.props.contactList}
                    onContactClick={this.setCurrentContact}
                />
                <AttendeeDetailForm
                    attendee={this.state.currentContact}
                    eventTuitionTable={this.props.eventTuitionTable}
                    confirmAction={this.props.confirmAction}
                    confirmButtonLabel="Add Attendee"
                    cancelAction={this.props.cancelAction}/>
            </div>
        );
    }
}

AttendeeRegistrationForm.defaultProps = {
    contactList: []
};



