module.exports = function formatPrice( val ) {
    val = parseInt( val );

    if( isNaN( val ) ) {
        return 'Price not available';
    }

    if( val > 0 ) {
        return `$${ val }`;
    }
    else {
        return 'FREE';
    }
};



