import {h, Component} from 'preact';
import Attendee from './Attendee';
import {fromQueryString} from 'util/ObjectUtils';

export default class AttendeeList extends Component {

    constructor( props ) {
        super( props );

        this.renderAttendees = this.renderAttendees.bind( this );

        this.state = {
            editAttendeeId: null
        };
    }

    componentWillMount() {
        // check queryString for attendee edit ID - eg. ?editattendee=123
        var editAttendeeId = fromQueryString( window.location.search ).editattendee;
        this.setState( {editAttendeeId} );
    }

    renderAttendees() {
        return this.props.attendeeList.map( attendee => {
            var tuition = this.props.eventTuitionTable[attendee.AttendeeType];

            return <Attendee
                key={attendee.ID}
                editMode={this.state.editAttendeeId == attendee.ID}
                eventTuitionTable={this.props.eventTuitionTable}
                removeAction={this.props.removeAction}
                editAction={this.props.editAction}
                tuition={tuition}
                attendee={attendee}
            />
        } )
    }

    render() {
        return (
            <ul className="attendee-list">
                {this.renderAttendees()}
            </ul>
        );
    }
}

AttendeeList.defaultProps = {
    editAction: () => {
    },
    removeAction: () => {
    }
};



