import Preact, {h, Component} from 'preact';
import classNames from 'classnames';
import ContactList from './ContactList';

export default class Contacts extends Component {

    constructor( props ) {
        super( props );

        this.toggleContactList = this.toggleContactList.bind( this );

        this.state = {
            contactsOpen: false
        };
    }

    toggleContactList( bool ) {
        this.setState( {
            contactsOpen: bool || !this.state.contactsOpen
        } );
    }

    render() {
        var containerClass = classNames(
            'contacts-container',
            'grid-unit',
            {'contacts-closed': !this.state.contactsOpen} );

        var contactTypes = this.props.contactList
            .reduce( ( acc, curr ) => {
                acc[curr.AttendeeType] && acc[curr.AttendeeType].push( curr );
                return acc;
            }, {'Doctor': [], 'Staff': []} );

        var closeContacts = () => this.toggleContactList( false );

        return (
            <div className={containerClass}>
                <span onClick={() => this.toggleContactList()} className="contacts-toggle">
                    <i className="icon-address-book"></i>
                </span>

                <h3 className="contacts-heading">Add a Doctor</h3>
                <ContactList
                    onClick={closeContacts}
                    onContactClick={this.props.onContactClick}
                    contactList={contactTypes['Doctor']}/>

                <h3 className="contacts-heading">Add a Staff Member</h3>
                <ContactList
                    onClick={closeContacts}
                    onContactClick={this.props.onContactClick}
                    contactList={contactTypes['Staff']}/>
            </div>
        );
    }
}

Contacts.defaultProps = {
    contactList: []
};



