import {h} from 'preact';

var EventDetail = ( {details} ) => {
    var {Title, WorkCity, WorkState, DisplayDate} = details;

    return (
        <div className="seminar-detail-summary">
            <h2>{Title}</h2>
            <ul className="horizontalList">
                <li className="seminar-location">{`${WorkCity}, ${WorkState}`}</li>
                <li className="seminar-date">{DisplayDate}</li>
                <li className="seminar-credits">{details['CE Credits']} CE Credits</li>
            </ul>
        </div>
    );
};

export default EventDetail;



