import {h, Component} from 'preact';
import AttendeeList from './AttendeeList';
import AttendeeRegistrationForm from './AttendeeRegistrationForm';
import EventDetail from './EventDetail';
import OrderSummary from './OrderSummary';
import {EventsService} from '../services/EventsService';
import {ContactsService} from '../services/ContactsService';
import {AttendeeService} from '../services/AttendeeService';
import {notifier} from '../util/notifier';
import {messages, strings} from '../util/constants';

var requestErrorNotifier = msg => {
    return reason => {
        msg = msg || messages.DEFAULT_ERROR_MSG;
        notifier.error( msg );
        throw new Error( reason );
    }
};

var {REGIONAL_EVENT, COMPLIMENTARY_EVENT} = strings;
var validateRegistration = {
    // rules: must have 1 doctor; limit to 2 doctors and 2 staff
    'Regional Event': attendeeList => {
        return true;
    },
    // rules: must have 1 doctor; limit to 2 doctors and 1 staff
    'Complimentary Event': attendeeList => {
        return true;
    }
};

export default class App extends Component {

    constructor( props ) {
        super( props );

        var {seminarId, accountNumber} = this.props;

        this.toggleRegistration = this.toggleRegistration.bind( this );
        this.renderRegistrationForm = this.renderRegistrationForm.bind( this );
        this.addAttendee = this.addAttendee.bind( this );
        this.removeAttendee = this.removeAttendee.bind( this );
        this.updateAttendee = this.updateAttendee.bind( this );

        this.state = {
            editMode: false,
            isLoaded: false,
            hasErrors: !seminarId || !accountNumber,
            eventDetails: {},
            eventTuitionTable: {
                // Sensible defaults
                'Doctor': 299,
                'Staff': 199
            },
            attendeeList: [],
            contactList: [],
            subtotal: 0
        };

        // request required services in order to initialize app

        Promise
            .all( [
                EventsService.getEventInfo( seminarId ),
                AttendeeService.getAll( seminarId )
            ] )
            .then(
                results => {
                    var eventDetails = results[0],
                        attendeeList = results[1],
                        eventTuitionTable = {
                            'Doctor': eventDetails['Dentist Tuition'],
                            'Staff': eventDetails['Staff Tuition']
                        };

                    this.setState( {
                        isLoaded: true,
                        eventDetails,
                        attendeeList,
                        eventTuitionTable,
                        subtotal: this.calculateSubtotal( attendeeList, eventTuitionTable )
                    } );
                },
                reason => {
                    console.error( 'Failed to initialize event registration - ' + reason );
                    this.setState( {
                        isLoaded: true,
                        hasErrors: true
                    } );
                }
            );

        ContactsService.getAll()
            .then(
                contactList => {
                    this.setState( {contactList} );
                },
                reason => {
                    console.error( 'Unable to get Contact List - ' + reason );
                }
            );

    }

    getChildContext() {
        return {
            getCurrentAttendeeList: () => {
                return this.state.attendeeList.map( attendee => attendee.Email );
            }
        }
    }

    toggleRegistration() {
        var editMode = !this.state.editMode;
        this.setState( {editMode} );
    }

    addAttendee( attendee ) {
        attendee.SeminarID = this.props.seminarId;

        return AttendeeService.add( attendee )
            .then(
                data => {
                    AttendeeService.getAll( this.props.seminarId )
                        .then(
                            attendeeList => {
                                this.setState( {
                                    attendeeList,
                                    subtotal: this.calculateSubtotal( attendeeList, this.state.eventTuitionTable )
                                } );
                            },
                            requestErrorNotifier( messages.FAIL_ADD_ATTENDEE_MSG )
                        );
                },
                requestErrorNotifier( messages.FAIL_ADD_ATTENDEE_MSG )
            );
    }

    removeAttendee( id ) {
        return AttendeeService.remove( id )
            .then(
                () => {
                    var updatedAttendeeList = this.state.attendeeList
                        .slice()
                        .filter( attendee => attendee.ID !== id );

                    this.setState( {
                        attendeeList: updatedAttendeeList,
                        subtotal: this.calculateSubtotal( updatedAttendeeList, this.state.eventTuitionTable )
                    } );
                },
                requestErrorNotifier( messages.FAIL_REMOVE_ATTENDEE_MSG )
            );
    }

    updateAttendee( attendee ) {
        // clone list => update local model
        return AttendeeService.update( attendee )
            .then(
                () => {
                    var updatedAttendeeList = this.state.attendeeList
                        .slice()
                        .map( item => {
                            if ( item.ID === attendee.ID ) {
                                item = Object.assign( item, attendee );
                            }
                            return item;
                        } );

                    this.setState( {
                        attendeeList: updatedAttendeeList,
                        subtotal: this.calculateSubtotal( updatedAttendeeList, this.state.eventTuitionTable )
                    } );
                },
                requestErrorNotifier( messages.FAIL_UPDATE_ATTENDEE_MSG )
            );
    }

    calculateSubtotal( attendeeList, tuitionTable ) {
        return attendeeList.reduce( ( prev, attendee ) => {
            return prev += parseInt( tuitionTable[attendee.AttendeeType] );
        }, 0 );
    }

    renderRegistrationForm() {

        let {
            props: {accountNumber, ueStatus},
            state: {eventDetails, eventTuitionTable, contactList}
        } = this;

        return this.state.editMode
            ? <AttendeeRegistrationForm
                accountNumber={accountNumber}
                sourceCode={eventDetails.SourceCode}
                ueStatus={ueStatus}
                eventTuitionTable={eventTuitionTable}
                contactList={contactList}
                cancelAction={this.toggleRegistration}
                confirmAction={this.addAttendee}/>
            : '';
    }

    renderLoader() {
        return <div className="loader paddedBlock">
            <p className="msg"><i></i> Loading registration details...</p>
        </div>;
    }

    renderErrorMessage() {
        return (
            <div>
                <div className="paddedBlock">
                    <h1 style={{fontSize: '36px', fontWeight: 400}}>We're sorry</h1>
                    <br/>
                    <p>We experienced an issue while trying to retrieve your attendee information for this event.</p>
                    <p>Please try refreshing your browser. If the problem continues, please contact our events customer
                        service by calling {strings.PHONE_EVENTS}</p>
                </div>
            </div>
        );
    }

    renderRegisterAttendeeAction() {
        let limit = 3;

        if ( this.state.attendeeList.length < limit ) {
            return (
                <div className="block-actions">
                    <span className="action link1" onClick={this.toggleRegistration}> + Add an Attendee</span>
                    {this.renderRegistrationForm()}
                </div>
            );
        }
        else {
            return (
                <p className="paddedBlock attendee-list-full-msg">{messages.ATTENDEE_LIMIT.replace( '%COUNT%',
                    limit )}</p>
            );
        }
    }

    hasRequiredAttendees() {
        return this.state.attendeeList.some( attendee => attendee.AttendeeType === 'Doctor' );
    }

    render() {
        if ( !this.state.isLoaded ) {
            return this.renderLoader();
        }

        if ( this.state.hasErrors ) {
            return this.renderErrorMessage();
        }

        var {eventDetails, eventTuitionTable, attendeeList} = this.state;

        return (
            <div ref={ref => this.main = ref}>
                <div className="row">
                    <div className="grid-unit">
                        <EventDetail details={eventDetails}/>
                    </div>
                </div>
                <div className="row">
                    <div className="grid-unit">
                        <div className="attendee-group module collapsedBox block--dark">
                            <h2 className="mod-hd">Registered Attendees</h2>
                            <div className="mod-bd">
                                <AttendeeList
                                    removeAction={this.removeAttendee}
                                    editAction={this.updateAttendee}
                                    eventTuitionTable={eventTuitionTable}
                                    attendeeList={attendeeList}/>
                                {this.renderRegisterAttendeeAction()}
                            </div>
                        </div>
                    </div>
                </div>

                <div className="cart-footer">
                    <div className="row">
                        <div className="grid-unit"></div>
                        <div className="grid-unit">
                            <OrderSummary subtotal={this.state.subtotal}
                                          nextStepDisabled={!this.hasRequiredAttendees()}
                                          footnote={this.state.eventDetails.CancellationPolicy}
                            />
                        </div>
                    </div>
                </div>
            </div>
        );
    }

}

