import Preact, {h, Component} from 'preact';
import Contact from './Contact';

export default class ContactList extends Component {

    renderContacts( contacts ) {
        return contacts.map( contact => {
            return <Contact
                key={contact.ID}
                id={contact.ID}
                fullName={`${contact.NameFirst} ${contact.NameLast}`}
                onClickHandler={this.props.onContactClick}
            />
        } );
    }

    render() {
        var contacts;

        if ( this.props.contactList && this.props.contactList.length ) {
            contacts = this.renderContacts( this.props.contactList );
        }
        else {
            contacts = <li><em>No previous registrants</em></li>;
        }

        return (
            <ul className="contacts-list" onClick={this.props.onClick}>
                {contacts}
            </ul>
        );
    }
}

ContactList.defaultProps = {
    onClick: () => {
    }
};



