import {h} from 'preact';
import classNames from 'classnames';
import formatPrice from '../util/formatPrice';
import {paths} from '../util/constants.js'

const BEFORE_CONTINUE_MSG = 'Please add at least 1 Doctor to your attendee list before continuing';

var OrderSummary = ( props ) => {

    var continueButtonClass = classNames( 'button', 'right', {
            disabled: props.nextStepDisabled
        } ),
        orderTotalClass = classNames( 'order-totals-container', 'module', {
            hidden: props.nextStepDisabled
        } ),
        handleContinue = e => {
            if ( props.nextStepDisabled ) {
                e.preventDefault();
                $( e.target ).showHint( BEFORE_CONTINUE_MSG );
                return false;
            }
        },
        message = ( props.nextStepDisabled )
            ? <p className="paddedBlock">{BEFORE_CONTINUE_MSG}</p>
            : '';

    return (

        <div>
            <div id="order-totals-container" className={orderTotalClass}>
                <div id="order-totals" className="order-totals">
                    <div className="order-subtotal">
                        <span className="label">Subtotal:</span>
                        <span className="value">{formatPrice( props.subtotal )}</span>
                    </div>
                </div>
            </div>

            <div className="cart-actions">
                {message}
                <a href={paths.EVENT_CHECKOUT} onClick={handleContinue.bind( this )} className={continueButtonClass}>Continue
                    <i className="icon-chevron-right"></i></a>
            </div>

            <div className="paddedBlock footnote hidden">
                <p>{props.footnote}</p>
            </div>
        </div>
    );
}

OrderSummary.defaultProps = {
    subtotal: 0,
    nextStepDisabled: true,
    footnote: ''
};

export default OrderSummary;




