import Preact, {h, Component} from 'preact';
import Attendee from './Attendee';
import classNames from 'classnames';
import formatPrice from '../util/formatPrice';
import {messages} from '../util/constants';
import {usStateList} from '../util/usStateList';
import {attendeeFactory} from '../util/models';
import Formsy from 'formsy-react';

import UpiInput from './form/UpiInput';
import UpiSelect from './form/UpiSelect';

// todo: AttendeeTitleList should be passed as pre-populated option list as prop so we don't have an additional model to
// deal with here
var attendeeTitleList = [
    {name: 'Doctor', type: 'Doctor'},
    {name: 'Hygienist', type: 'Staff'},
    {name: 'CDT', type: 'Staff'},
    {name: 'EFDA', type: 'Staff'},
    {name: 'Auxiliary', type: 'Staff'},
    {name: 'Office Manager', type: 'Staff'}
];

Formsy.addValidationRule( 'isUnique', ( values, value, array ) => {
    // map over current attendee list and check if email address is not already used
    if ( array.map( item => item.toLowerCase() ).indexOf( value.toLowerCase() ) !== -1 ) {
        return messages.VALIDATE_UNIQUE_ATTENDEE_MSG;
    }

    return true;
} );

const regexpList = {
    phone: /^(?:(?:\+?1\s*(?:[.-]\s*)?)?(?:\(\s*([2-9]1[02-9]|[2-9][02-8]1|[2-9][02-8][02-9])\s*\)|([2-9]1[02-9]|[2-9][02-8]1|[2-9][02-8][02-9]))\s*(?:[.-]\s*)?)?([2-9]1[02-9]|[2-9][02-9]1|[2-9][02-9]{2})\s*(?:[.-]\s*)?([0-9]{4})(?:\s*(?:#|x\.?|ext\.?|extension)\s*(\d+))?$/
};

class AttendeeDetailForm extends Component {

    constructor( props ) {
        super( props );

        this.handleSubmit = this.handleSubmit.bind( this );
        this.enableSubmit = this.enableSubmit.bind( this );
        this.disableSubmit = this.disableSubmit.bind( this );
        this.setAttendeeType = this.setAttendeeType.bind( this );
        this.getAttendeeType = this.getAttendeeType.bind( this );
        this.resetForm = this.resetForm.bind( this );

        this.state = {
            isValid: false,
            hasSubmitted: false,
            isDisabled: true,
            isProcessing: false,
            attendee: attendeeFactory.create( props.attendee )
        };

    }

    componentWillReceiveProps( nextProps ) {
        if ( nextProps.attendee !== this.state.attendee ) {
            this.setState( {
                attendee: nextProps.attendee
            } );
        }
    }

    setAttendeeType( attendeeTitle ) {
        var attendee = attendeeFactory.create( this.state.attendee );
        attendee.AttendeeType = this.getAttendeeType( attendeeTitle );

        this.setState( {attendee} );
    }

    getAttendeeType( attendeeTitle ) {
        var position = attendeeTitleList.reduce( ( prev, curr ) => {
            return ( attendeeTitle === curr.name ) ? curr : prev;
        }, {} );

        // if a title gets entered from the backend that the client does not recognize based on the provided map
        // assume the attendee type to be "Doctor"
        return position.type || 'Doctor'; // default to doctor
    }

    handleSubmit( formData ) {
        if ( this.state.isDisabled ) {
            return;
        }

        this.toggleProcessing();

        var attendee = Object.assign( {}, this.state.attendee, formData );
        console.log( '[Info]: Attendee', attendee );

        this.props.confirmAction( attendee )
            .then( this.resetForm )
            .catch( err => this.toggleProcessing() );
    }

    toggleProcessing( bool ) {
        var isProcessing = bool || !this.state.isProcessing;
        this.setState( {isProcessing} );
    }

    resetForm() {
        this.setState( {
            attendee: attendeeFactory.create( {
                AccountNumber: this.props.accountNumber
            } ),
            editMode: false,
            isProcessing: false
        } );

        this.props.cancelAction();
    }

    disableSubmit() {
        this.setState( {isDisabled: true} );
    }

    enableSubmit() {
        this.setState( {isDisabled: false} );
    }

    renderOfficeTitleOptions( selectedValue ) {
        var optionList = attendeeTitleList.map( item => {
            return <option key={item.name}
                           value={item.name}>{`${item.name} - ${ formatPrice( this.props.eventTuitionTable[item.type] ) }`}</option>;
        } );

        return (
            <UpiSelect required
                       label="Office Position"
                       name="AttendeeTitle"
                       value={selectedValue}
                       onUpdate={this.setAttendeeType}>
                <option value="">Select one...</option>
                {optionList}
            </UpiSelect>
        );
    }

    renderStateSelectOptions( optionState ) {
        var optionList = usStateList.map( state => {
            return <option key={state.name} value={state.abbreviation}>{state.name}</option>;
        } );

        return (
            <UpiSelect required label="State" name="State" value={optionState}>
                <option value=""></option>
                {optionList}
            </UpiSelect>
        );
    }

    render() {
        var {NameFirst, NameLast, Email, PhoneWork, AttendeeTitle, Address1, City, State, Zip}
                = this.state.attendee,

            formClass = classNames( 'attendee-form-container', 'grid-unit', {processing: this.state.isProcessing} ),
            submitButtonClass = classNames( 'button', {disabled: this.state.isDisabled} );

        return (
            <div className={formClass}>
                <Formsy.Form onSubmit={this.handleSubmit} onValid={this.enableSubmit} onInvalid={this.disableSubmit}>
                    <div className="attendee-form">
                        <fieldset>
                            <legend className="h2">Attendee Details</legend>
                            <ul>
                                <li className="field-office-position">
                                    {this.renderOfficeTitleOptions( AttendeeTitle )}
                                </li>
                                <li className="field-fname">
                                    <UpiInput required
                                              value={NameFirst}
                                              name="NameFirst"
                                              label="First Name"
                                              validations="isExisty"
                                              validationError="First name is required"/>
                                </li>
                                <li className="field-lname">
                                    <UpiInput required
                                              value={NameLast}
                                              name="NameLast"
                                              label="Last Name"
                                              validationError="Last name is required"/>
                                </li>
                                <li className="field-Email">
                                    <UpiInput required
                                              value={Email}
                                              type="email"
                                              name="Email"
                                              label="Email"
                                              validations={{
                                                  isUnique: excludeFromList( this.props.attendee.Email,
                                                      this.context.getCurrentAttendeeList() ),
                                                  isEmail: true
                                              }}
                                              validationError="This is not a valid email address"/>
                                </li>
                                <li className="field-Phone">
                                    <UpiInput required
                                              value={PhoneWork}
                                              name="PhoneWork"
                                              label="Work Phone"
                                              validations={{matchRegexp: regexpList.phone}}
                                              validationError="Please enter a valid 10 digit phone number (optional extension as x123)"/>
                                </li>
                            </ul>
                        </fieldset>

                        <fieldset>
                            <legend className="h2">Mailing Address</legend>
                            <ul>
                                <li className="field-addr1">
                                    <UpiInput required
                                              value={Address1}
                                              name="Address1"
                                              label="Street"
                                              validationError="Street address is required"/>
                                </li>
                                <li className="field-City">
                                    <UpiInput required
                                              value={City}
                                              name="City"
                                              label="City"
                                              validationError="City is required"/>
                                </li>
                                <li className="field-State">
                                    {this.renderStateSelectOptions( State )}
                                </li>
                                <li className="field-Zip">
                                    <UpiInput required
                                              value={Zip}
                                              name="Zip"
                                              label="ZIP Code"
                                              validationError="ZIP Code is required"/>
                                </li>
                            </ul>
                        </fieldset>
                        <div className="form-actions">
                            <div className="button secondary"
                                 onClick={this.props.cancelAction}>{this.props.cancelButtonLabel}</div>
                            <button className={submitButtonClass} type="submit">
                                {this.props.confirmButtonLabel} <i className="icon-chevron-right"></i>
                            </button>
                        </div>
                    </div>
                </Formsy.Form>
            </div>
        );
    }
};

// AttendeeDetailForm.contextTypes = {
//     getCurrentAttendeeList: React.PropTypes.func
// }

AttendeeDetailForm.defaultProps = {
    cancelButtonLabel: 'Cancel',
    confirmButtonLabel: 'Submit',
    attendee: attendeeFactory.create()
};

/** Helpers */
function excludeFromList( target, list ) {
    var i = list.indexOf( target );
    if ( i !== -1 ) {
        list.splice( i, 1 );
    }
    return list;
}

export default AttendeeDetailForm;



