import {h, Component} from 'preact';
import classNames from 'classnames';
import AttendeeDetailEditForm from './AttendeeDetailEditForm';
import formatPrice from '../util/formatPrice';


export default class Attendee extends Component {

    constructor( props ) {
        super( props );

        this.toggleEdit = this.toggleEdit.bind( this );
        this.handleRemove = this.handleRemove.bind( this );

        this.state = {
            isProcessing: false,
            editMode: this.props.editMode
        }
    }

    toggleEdit() {
        this.setState({
            editMode: !this.state.editMode
        });
    }

    handleRemove() {
        if( confirm( 'Remove this attendee?' ) ) {
            this.setState({ isProcessing: true });
            this.props.removeAction( this.props.attendee.ID )
                .catch( () => { this.setState({ isProcessing: false }); });
        }
    }

    renderEditForm() {
        return <AttendeeDetailEditForm
            eventTuitionTable={ this.props.eventTuitionTable }
            attendee={ this.props.attendee }
            confirmAction={ this.props.editAction }
            cancelAction={ this.toggleEdit } />;
    }

    renderActions() {
        return (
            <div className="attendee-actions">
                <span onClick={ this.toggleEdit } className="link1">Edit</span>
                <span onClick={ this.handleRemove } className="link1">Remove</span>
            </div>
        );
    }

    render() {
        var { attendee } = this.props,
            attendeeClass = classNames( 'attendee', 'noselect', { processing: this.state.isProcessing });

        return (
            <li className={ attendeeClass }>
                <div className="attendee-summary">
                    <div className="attendee-total">
                        <span className="attendee-total-type">{ upi.String.toTitleCase( attendee.AttendeeType || '' ) }</span>
                        <span className="attendee-total-price">{ formatPrice( this.props.tuition ) }</span>
                    </div>
                    <div className="attendee-name">
                        { `${ this.props.attendee.NameFirst } ${ this.props.attendee.NameLast }` }
                    </div>
                    { !this.state.editMode && this.renderActions() }
                </div>
                { this.state.editMode && this.renderEditForm() }
            </li>
        );
    }
}


