import 'babel-polyfill';

import '../../css/checkout-seminars.scss';

import {h, render} from 'preact';

import Main from './components/Main';

$( function () {

    //initialize applicatin level properties
    var validator,
        accountNumber = $( '#account_number' ).val(),
        seminarId = $( '#seminar_id' ).val(),
        ueStatus = $( '#ue_status' ).val(),
        seminarRegistrationNode = document.getElementById( 'seminar-registration-component' );

    // initialize registration if on step 1
    if ( seminarRegistrationNode ) {
        return render( <Main
            accountNumber={accountNumber}
            ueStatus={ueStatus}
            seminarId={seminarId}/>, seminarRegistrationNode );
    }

    validator = $( '#aspnetForm' ).validate();

    // toggle new credit card form
    function toggleCreditCardForm ( target ) {
        validator.reset();
        var isNewCard = target.value === 'NewPayment';
        $( '#newCreditCardForm' ).toggleClass( 'selected', isNewCard );
    }

    var paymentRadios = $( 'input:radio[name="payment"]' );
    paymentRadios.on( 'change', e => toggleCreditCardForm( e.currentTarget ) );
    // toggle credit card form on page load if selected
    if ( paymentRadios.length ) {
        toggleCreditCardForm( paymentRadios.filter( ':checked' )[0] );
    }

    $( '#aspnetForm' ).find( 'input[id*="PromoCode"]' ).on( 'keypress', function ( e ) {
        // cancel submit from promo field
        if ( e.which == 13 ) {
            return false;
        }
    } );

    $( 'input, select' ).on( 'keypress click', ( e ) => {
        if ( !validator ) {
            return;
        }

        let target = e.currentTarget,
            inputType = target.getAttribute( 'type' );

        // let all keypresses fall through unless pressing enter
        if ( e.type === 'keypress' && e.which !== 13 ) {
            return true;
        }

        // if pressing enter validate current field and cancel submit event so form cannot be submitted from credit fields
        if ( e.type === 'keypress' && e.which === 13 ) {
            e.preventDefault();
            validator.element( target );
        }

        if ( e.type === 'click' && (inputType && inputType.toLowerCase() === 'submit') ) {
            // if a submit button was clicked then manually issue form validation
            validator.form();

            // cancel form submission if form is invalid
            if ( !validator.valid() ) {
                // prevent double postback
                e.preventDefault();
            }

            // patch: safari halts all execution on form post - need short delay in order to display ui processing indicators
            setTimeout( () => {
                let targetId = target.getAttribute( 'name' );

                // if form is valid, do post passing unique button id who issued post
                if ( validator.valid() && targetId ) {
                    var msg = (targetId.toLowerCase().indexOf( 'promo' ) > -1) ? 'Applying Promo Code' : null;
                    $( 'body' ).prepend( upi.html.processOrderDialog( msg ) );
                    __doPostBack( targetId );
                }
            }, 200 );

            return false;
        }
    } )

} );



